@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
html, body{
  height: 100%;
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}   
.container {
  height: 100vh;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background-color: #251237;
}
.keyboard-wrapper {
  height: 360px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.keyboard-wrapper[data-theme="light"] {
  background-color: #fff;
}

.keyboard-wrapper .row {
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: center;
}

.keyboard-wrapper .letter {  
  display: -webkit-inline-flex;  
  display: inline-flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  background-color: #312344;
  margin: 5px;
  width: 80px;
  height: 80px;
  border-radius: 8px;
}

.keyboard-wrapper .letter span {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
}

.keyboard-wrapper .space-bar{
  width: 500px;
}


 
.keyboard-wrapper .active-letter {
  background-color: #d12e2c;
  color: white;
}

.accuracy-wrapper p{
  font-size: 2em;
  color: #ffffff;
}
.text-wrapper .display-text{
  /* color: yellow; */
}

.text-wrapper .current-letter{
  border-bottom: 2px solid #fff;
  /* color:black */
}

.main-content {
  border: 3px solid red;
  height: 700px;
  width: 1500px;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
}

.main-content .left {
  border: 2px solid yellow;
  width: 250px;
}

.main-content .center {
  border: 2px solid purple;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.main-content .center .top {
  border: 2px solid violet;
}

.main-content .center .bottom {
}

.main-content .right {
  border: 2px solid greenyellow;
  width: 250px;
}

